<template>
    <div class="product-type-param-ratio">
        <ul class="ratio" @click="handleItemClick">
            <li v-for="item in options" :key="item" :data-value="item.name" :class="{ active: selectRatio === item.name }" class="ratio-item">
                <span class="name">{{ item.name }}</span>
                <span class="desc"></span>
                <span class="price">{{ item.price }}</span>
            </li>
        </ul>
        
    </div>
</template>

<script>
import { defineComponent } from 'vue'
import { getParentNode } from '../../utils/tool'

export default defineComponent({
    name: 'ProductTypeRatio',
    components: {
        
    },
    props: {
        options: {
            type: Array,
            required: true
        },
        selectRatio: {
            value: String,
            required: true
        }
    },
    emits: ['change'],
    setup(props, context) {
        const handleItemClick = (event) => {
            const node = getParentNode(event.target, 'ratio-item')
            if (node) {
                context.emit('change', {
                    value: props.options.find(item => item.name === node.attributes['data-value'].value)
                })
            }
        }
        return {
            handleItemClick
        }
    }
})
</script>
<style lang="less" scoped>
.product-type-param-ratio {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    box-sizing: border-box;
    .ratio {
        width: 100%;
        .ratio-item {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            flex-basis: 100%;
            padding: 10px 0;
            border: solid 2px rgba(237,237,237);
            font-size: 15px;
            line-height: 1.9;
            margin-bottom: 5px;
            .name {
                font-weight: bold;
            }
            .desc {
                line-height: 1.3;
            }
            .price {
                color: #777;
            }
        }
        .active {
            border-color: rgb(83, 117, 221);
        }
    }
}
</style>
