/**
 * elementclassName
 * @param {*} element
 * @param {string} className
 * @returns element
 */
 export function getParentNode(element, className) {
    while (element) {
        if (element.className && element.className.indexOf(className) > -1) {
            return element;
        } else {
            element = element.parentNode;
        }
    }
    return null;
}